<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户手机号码、姓名"
                      @keyup.native.enter="conditionQuery"
                      v-model="currencyTrans.keyword">
            </el-input>
            <span>虚拟币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.currency">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="conditionQuery" size="small" type="primary" style="margin-left: 30px">查询
            </el-button>
        </div>
        <el-table
                ref="singleTable"
                :data="tableData">
            <el-table-column
                    type="index"
                    label="序号"
                    width="90">
            </el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="currencyTrans.page"
                           :page-sizes="[15,30,45,60]" :page-size="currencyTrans.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {currencyall} from '@/api/useraddress';

    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    export default {
        data() {
            return {
                total: 0,
                allCoin,
                tableData: [],
                coinList: [],
                currencyTrans: {
                    //标识为虚拟币查询
                    type: '',
                    keyword: '',
                    currency: 0,
                    page: 1,
                    pageSize: 15
                },
                tableHeader: [{
                    label: "UID",
                    prop: "walletSeq",
                    width: 90
                }, {
                    label: "姓名",
                    prop: "realName",
                    width: 180
                }, {
                    label: "手机号码",
                    prop: "phoneNum",
                }, {
                    label: "用户名",
                    prop: "alaisName",
                    width: 100
                }, {
                    label: "虚拟币类型",
                    prop: "coinName",
                    width: 180,
                    align: 'center'
                }, {
                    label: "总金额",
                    prop: "cashAmount",
                    width: 180
                }, {
                    label: "挂单金额",
                    prop: "ordersAmount",
                }, {
                    label: "冻结金额",
                    prop: "freezeMcnt",
                    width: 180
                }, {
                    label: "可用金额",
                    prop: "availMoney",
                    width: 180
                }]
            }
        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },
        methods: {
            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            },
            conditionQuery() {
                this.getList();
            },
            //查询所有列表以及条件查询
            getList() {
                currencyall(this.currencyTrans).then(response => {
                    this.total = response.data.total;
                    this.tableData = response.data.list;
                    this.coinList = response.data.coinList;
                    this.coinList.unshift(this.allCoin);
                    if (this.total <= 0) {
                        this.msg = "未查询到此筛选数据";
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                })
            }
        }, created() {
            this.getList();
        }
    }
</script>


